<template>
    <div>
         
        <div class="box">
            <div class="header">
                <div class="arrow-left fl" @click="back" >            
                   <van-icon name="arrow-left" size="30px"/>                  
                </div>

                <div class="right-box fr">
                    <div class="search fl">
                        <van-icon name="search" size="30px"/>
                    </div>

                    <div class="ellipsis fl">
                        <van-icon name="ellipsis" size="30px"/>
                    </div>
                </div>
            </div>
            

            <div class="content-box">
                <div class="title">{{title}}</div>

                <div class="icon-box">
                    <div class="img fl">
                        <img class="auto-img" :src="img" alt="">
                    </div>
                    <div class="source">来源:{{source}}</div>
                </div>


                <div v-html="text" class="content"></div>


            </div>

            <div class="footer">
                <div class="input fl">
                    <input type="text" placeholder="写评论">
                </div>

                <div class="icon fl">
                    <van-icon name="chat-o" size="30px" color="#595959"/>
                    <van-icon v-show=isShow name="star-o" size="30px" color="#595959" @click="collection()"/>
                    <van-icon v-show=!isShow name="star" size="30px" color="#595959" @click="collection()"/>
                    <van-icon name="good-job-o" size="30px" color="#595959"/>
                    <van-icon name="share" size="30px" color="#595959"/>

                </div>

            </div>

            <div>
                <router-view />
            </div>
        </div>    
    </div>
</template>

<script>
    export default {
        name:'newsDetails',

        data(){

            return {
                // 数据
                data:'',

                // 请求数据id
                id:'',

                item:'',

                // 文本
                text:'',

                // 标题
                title:'',

                // 图片
                img:'',

                // 来源
                source:'',

                // 收藏的标志
                isShow:true
            }
        },

        created(){
            //加载提示
            this.$toast.loading({
                duration: 2000,
                message: "加载中..."
            });

            // console.log('this.$route.params ==>',this.$route.params)
            // 获取路由传参的值
            this.id = this.$route.params.id
            // console.log('id ==>',this.id)

            this.item = this.$route.params.item
            // console.log('item ==>',this.item)
            let _this = this

             //根据路由参数请求新闻详情数据
            this.axios({
                //请求类型
                method:'GET',
                //请求地址
                url:'http://api01.6bqb.com/toutiao/detail?apikey=9152B061C54746870D248AA78B33EA05&itemId=' + _this.id
                
            }).then(result =>{
                console.log('result111111==>',result)
                _this.data = result.data
                // console.log('_this.data ==>',_this.data)

                let reg = /(<[a-z]*.*\s*\/?>.*\s*<\/[a-z]>)/g;

                let a = reg.test(_this.data.data.content);
                // console.log('a ==> ', a);

                let d = RegExp.$1;
                // console.log('d ==> ', d);

                // 文本内容
                _this.text = d

                //标题
                _this.title = _this.data.data.title
                // 图标
                _this.img = _this.data.data.media_user.avatar_url
                // 来源
                _this.source = _this.data.data.media_user.screen_name

                //取消加载提示
                this.$toast.clear();


            }).catch(err => {
                //取消加载提示
                this.$toast.clear();
            })

        },

        updated(){
            let _this = this
             // 获取历史数据
            let collectionData = localStorage.getItem('collectionData')

            // 防止空用户
            collectionData = collectionData ? JSON.parse(collectionData) : [];

            // 数据等新之后判断是否已经被收藏
            for(let i=0; i<collectionData.length; i++){
                if(collectionData[i].item_id === _this.item.item_id){
                    _this.isShow = false
                }
            }
        },



        methods:{

            //返回上一级
            back(){
                this.$router.go(-1)
            },

            // 收藏
            collection(){
                this.isShow = !this.isShow

                if(!this.isShow){
                    // 获取历史数据
                    let collectionData = localStorage.getItem('collectionData')
                    
                    // 防止空用户
                    collectionData = collectionData ? JSON.parse(collectionData) : [];

                    this.item.isShow = this.isShow

                    // 增加到数组中
                    collectionData.unshift(this.item)

                    // 保存在本地
                    localStorage.setItem('collectionData',JSON.stringify(collectionData));
                } else if(this.isShow) {

                    // 获取历史数据
                    let collectionData = localStorage.getItem('collectionData')
                    
                    // 防止空用户
                    collectionData = collectionData ? JSON.parse(collectionData) : [];

                    // 取消收藏
                    for(let i=0; i<collectionData.length; i++){
                        if(collectionData[i].item_id === this.item.item_id){                   
                        collectionData.splice(i,1)
                        }
                    }
                    // 保存在本地
                    localStorage.setItem('collectionData',JSON.stringify(collectionData));    
                }
            }
        }
    }
</script>

<style lang="less" scoped>

    .arrow-left{
        margin-top: 24px;
        border-bottom: 1px solid #f5f5f5;
    }

    .right-box{
        margin-top: 24px;
    }

    .ellipsis{
        margin-right: .3rem;
    }
    .search{
       margin-right: .3rem; 
    }

    .content-box{
        width: 100%;
        height: 14.8rem;
        overflow: hidden;
        overflow-y: auto;

        .title{
            font-size: .6rem;
            width: calc(100% - .6rem);
            padding: 0 .3rem;
        }

        .icon-box{
            width: calc(100% - .6rem);
            height: 1rem;
            padding: 0 .3rem;
            .img{
                width:1rem;
                height: 1rem;
                border-radius: 50%;
                margin-right: .3rem;
                >img{
                    border-radius: 50%;
                }
            }
            .source{
                line-height: 1rem;
                margin-left: .4rem;
                font-size: .4rem;
                font-weight: bold;
            }
        }
    }

    .content{
        width: calc(100% - .6rem);
        font-size: .4rem;
        padding: 0 .3rem;

        .pgc-img{
            width: 375px !important;
            height: 100px !important;

            >img{
                width: 100px !important;
                height: 100px !important;
            }
        }
    }

    .footer{
        width: 100%;
        height: 1.5rem;
        background: #f5f5f5;

        .input{
            height: 1rem;
            width: 4rem;
            >input{
                width: 100%;
                height: 100%;
                border: none;
                border-radius: .5rem;
                text-indent:.5rem;
                margin-top: .25rem;
                margin-left: .3rem;
                background: #ddd;
            }
        }

        .icon{
            margin-top: .4rem;
            margin-left: .3rem;
            .van-icon{
                margin-left: .5rem;
            }
        }
    }

</style>